import React, { lazy } from 'react'
import './techs-stack.scss'
import TechBubble from '../../../components/TechBubble'
import getTech from '../../../services/techs'

const TechsStack = ({ icon, name, techs}) => {
  const Icon = lazy(() => import(`../../../components/icons/${icon}`))
  return (
    <div className="techsStack">
      <Icon />
      <h3>{name}</h3>
      {techs.map(item => {
        const tech = getTech(item)
        return <TechBubble {...tech} key={tech.id} />
      })}
    </div>
  )
}

export default TechsStack
