// Techs Component

import React from 'react'
// import './techs.module.scss'
import {techs} from '../../services/techs'
import TechsStack from './TechsStack'
import Stacks from '../../components/Stacks'

const Techs = () => {
  return (
    <div className="techs">
      <h2>Tecnologías</h2>
      <Stacks>
        {techs.stacks.map(stack => {
          return <TechsStack
                  key={stack.id}
                  name={stack.name}
                  icon={stack.icon}
                  techs={stack.techs}
                />
        })}
      </Stacks>
    </div>
  )
}

export default Techs
